



























import HeadTtop from '../../../../components/head/head.vue'
export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '帮助中心',
			backshow: true,
			topsrc: '../../../../static/images/back.png',
		}
	},
	methods:{
		details(type){
			let tit = '';
			if( type === 3 ){
				tit = '配送说明'
			}else if( type === 4 ){
				tit = '售后服务'
			}else if( type === 5 ){
				tit = '客户服务'
			}else{
				tit = '关于APP'
			}
			uni.navigateTo({
				url:'/pages/info/help/details/details?type=' + type + '&tit=' + tit
			})
		}
	}
}
